@use '../../../styles/shared/truncate';

:host {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-radius: var(--token-border-radius-md);
    padding: 6px 8px;
    text-align: left;
    cursor: pointer;
    font-size: var(--token-font-size-button);
    transition:
        background-color var(--token-transition-time-fast),
        color var(--token-transition-time-fast),
        box-shadow var(--token-transition-time-fast);

    .content {
        display: flex;
        align-items: center;
        flex-grow: 1;
        gap: 8px;
        @extend %truncate-text;
    }

    .badge {
        position: absolute;
        top: -8px;
        left: 22px;
        z-index: 20;
        pointer-events: none;
        padding: 2px 2px;
        font-size: 10px;
    }

    .label {
        z-index: 10;
        font-weight: var(--token-font-weight-med);
        @extend %truncate-text;
    }

    .icon {
        font-family: 'Material Icons';
        font-style: normal;
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 1;
        font-size: var(--token-font-size-icon-lg) !important;
        z-index: 10;
    }

    .focus-indicator {
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: var(--token-sidebar-foreground);
        border-radius: var(--token-border-radius-md);
        opacity: 0;
        transition: background-color var(--token-transition-time-fast);
    }

    &:hover [data-name='focus-indicator'] {
        opacity: 0.1;
    }

    &:focus-visible {
        outline: none;
        box-shadow: 0 0 0 2px
            color-mix(
                in srgb,
                var(--token-state-active-foreground) 60%,
                transparent
            );
    }

    &[data-state='active'] {
        background-color: var(--token-state-active-foreground);
    }

    &.depth-0 {
        font-weight: var(--token-font-weight-light);
    }

    &.depth-1 {
        padding-left: 36px;
        font-weight: var(--token-font-weight-light);
    }

    &.depth-2 {
        padding-left: 50px;
        font-weight: var(--token-font-weight-light);
    }

    &.depth-3 {
        padding-left: 64px;
        font-weight: var(--token-font-weight-light);
    }
}

:host(.submenu-hover-content) {
    .label.sc-at-sidebar-menuitem {
        color: var(--token-sidebar-foreground);
        font-weight: var(--token-font-weight-normal);
    }
}
