import { Component, Prop, h, Host, Element } from '@stencil/core';

/**
 * @category Navigation
 * @description A sidebar menu item component for the sidebar.
 *
 * @slot label: Content displayed at the left of the menu item.
 * @slot actions: Content displayed at the right of the menu item.
 */

@Component({
    tag: 'at-sidebar-menuitem',
    scoped: true,
    styleUrl: 'at-sidebar-menuitem.scss',
})
export class AtSidebarMenuitemComponent {
    /**
     * Label to be displayed for the menu item
     */
    @Prop() label?: string;

    /**
     * Icon to be displayed with the label
     */
    @Prop() icon?: string;

    /**
     * Alert badge for the menu item
     */
    @Prop() badge?: string;

    /**
     * Defines menuitem styling for font weight and indent 0-3  Default 0
     */
    @Prop() depth?: number;

    /**
     * Will change style to indicate menuitem is active when set
     */
    @Prop({ mutable: true }) is_active?: boolean;

    @Element() el: HTMLAtSidebarMenuitemElement;

    private handleKeyDown = (event: KeyboardEvent): void => {
        if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            this.el.click();
        }
    };

    render() {
        return (
            <Host
                role="menuitem"
                data-state={this.is_active ? 'active' : 'inactive'}
                tabIndex={0}
                class={this.depth ? `depth-${this.depth}` : ''}
                onKeyDown={this.handleKeyDown}
            >
                <div
                    class="content"
                    data-name="content"
                >
                    <slot
                        name="label"
                        data-name="label"
                    />
                    {this.badge && (
                        <at-badge
                            class="badge"
                            data-name="badge"
                            role="presentation"
                            type="error"
                            impact="high"
                            rounded={false}
                            label={this.badge}
                        ></at-badge>
                    )}
                    {this.icon && <i class="icon">{this.icon}</i>}
                    {this.label && (
                        <span
                            data-name="label"
                            class="label"
                        >
                            {this.label}
                        </span>
                    )}
                </div>
                <slot name="actions" />
                <div
                    class="focus-indicator"
                    data-name="focus-indicator"
                    role="presentation"
                ></div>
            </Host>
        );
    }
}
